function hasShownProfileNux() {
  return parseBool(localStorage.getItem('hasShownProfileNux'));
}

function hasShownProjectNux() {
  return parseBool(localStorage.getItem('hasShownProjectNux'));
}

function setProfileNuxShown() {
  localStorage.setItem('hasShownProfileNux', 'true');
}

function setProjectNuxShown() {
  localStorage.setItem('hasShownProjectNux', 'true');
}

function parseBool(bool) {
  return bool === 'true' || false;
}

export default {
  hasShownProfileNux,
  hasShownProjectNux,
  setProfileNuxShown,
  setProjectNuxShown
};